import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Divider from "@material-ui/core/Divider";
import "./MyOrders.css";
import { Link } from "react-router-dom";
import Button from "@material-ui/core/Button";
import TodayIcon from "@material-ui/icons/Today";
import Typography from "@material-ui/core/Typography";
import LocalShippingIcon from "@material-ui/icons/LocalShipping";
import ClassTwoToneIcon from "@material-ui/icons/ClassTwoTone";
import Spinner from "../UI/Spinner";
import { API, Auth } from "aws-amplify";
import CardContent from "@material-ui/core/CardContent";
import { Grid, Card } from "@material-ui/core";

var today = new Date();
var dd = String(today.getDate()).padStart(2, "0");
var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
var yyyy = today.getFullYear();

today = yyyy + "-" + mm + "-" + dd;

const useStyles = makeStyles((theme) => ({
  "@keyframes blinker": {
    from: { opacity: 1 },
    to: { opacity: 0.2, color: "#3f51b5", fontWeight: "400" },
  },
  button: {
    marginBottom: theme.spacing(1),
  },
  allocationButton: {
    animationName: "$blinker",
    animationDuration: "0.7s",
    animationTimingFunction: "linear",
    animationIterationCount: "infinite",
    marginBottom: theme.spacing(1),
    marginRight: theme.spacing(2),
  },

  title: {
    fontSize: 20,
    height: 50,
    padding: 10,
    paddingLeft: 55,
    borderBottomStyle: "solid",
    borderWidth: "1px",
  },
}));

const MyOrders = () => {
  const classes = useStyles();
  const [activeOrders, setActiveOrders] = useState([]);
  const [loading, setLoading] = useState(false);
  useEffect(async () => {
    setLoading(true);

    var currentUser = await Auth.currentUserInfo();
    var currentUsername = currentUser.username;
    console.log(currentUsername);
    // var temp = await API.get("GoFlexeOrderPlacement",`/serviceorder?username=${currentUsername}`);
    var temp = await API.get(
      "GoFlexeOrderPlacement",
      `/customerorder/customer/${currentUsername}`
    );
    // const url='https://2n3n7swm8f.execute-api.ap-south-1.amazonaws.com/draft0/customerorder/customer/gaurav@gmail.com';
    console.log(temp);
    setActiveOrders(temp);
    setLoading(false);
  }, []);

  let orderList = (
    <section className="">
      {activeOrders.map((eachOrder) => (
        <div>
          <Card
            style={{
              margin: 0,
              marginTop: 20,
            }}
          >
            <CardContent
              style={{
                marginTop: 0,
                padding: 16,
              }}
            >
              <Grid container spacing={0}>
                <Grid item xs={2}>
                  <figure
                    style={{
                      marginTop: 0,
                      marginBottom: 0,
                      padding: 10,
                    }}
                  >
                    <img
                      alt="truck"
                      src="data:image/svg+xml;base64,"
                    />
                  </figure>
                </Grid>
                <Grid
                  item
                  xs={6}
                  style={{
                    marginTop: 15,
                    marginBottom: 20,
                  }}
                >
                  <Grid container spacing={0}>
                    <Grid item xs={12} style={{}}>
                      <h6
                        style={{
                          marginBottom: 25,
                          marginRight: 40,
                        }}
                      >
                        Order Id : <span>{eachOrder.OrderId}</span>
                      </h6>
                    </Grid>
                    <Grid item xs={6}>
                      <h6>
                        Order Date : <span>{eachOrder.pickupdate}</span>
                      </h6>
                    </Grid>
                    <Grid item xs={6}>
                      <h6>
                        Total Amount : {Math.floor(Math.random() * 100000)}
                      </h6>
                    </Grid>
                  </Grid>
                </Grid>
                {/* <Grid
                  item
                  xs={3}
                  style={{
                    marginTop: 15,
                    marginBottom: 20,
                  }}
                >
                  <h6
                    style={{
                      marginBottom: 25,
                    }}
                  > */}
                {/* date in dd/mm/yyyy format */}
                {/* var today = new Date(eachOrder.pickupdate); 
                    var dd = today.getDate(); var mm = today.getMonth()+1; //January is 0! var yyyy = today.getFullYear(); if(dd<10){dd='0'+dd} if(mm<10){mm='0'+mm} var today = dd+'/'+mm+'/'+yyyy; document.getElementById("DATE").value = today; */}
                {/* Order Date : <span>{eachOrder.pickupdate}</span>
                  </h6>
                  <h6>Total Amount : {Math.floor(Math.random() * 100000)}</h6>
                </Grid> */}
                <Grid item xs={3}>
                  <Grid container spacing={0}>
                    <Grid item xs={5}></Grid>
                    <Grid item xs={6}>
                      <Button
                        style={{
                          maxWidth: "150px",
                          minWidth: "150px",
                          maxHeight: "50px",
                          marginTop: 5,
                          marginBottom: 7,
                        }}
                        component={Link}
                        to="/track"
                        variant="contained"
                        color="default"
                        //   className={classes.allocationButton}
                        startIcon={<LocalShippingIcon />}
                      >
                        Track
                      </Button>
                      <Button
                        style={{
                          minWidth: "150px",
                          maxWidth: "150px",
                          maxHeight: "50px",
                          marginTop: 7,
                          marginBottom: 5,
                        }}
                        component={Link}
                        to={`order/${eachOrder.OrderId}`}
                        variant="contained"
                        color="default"
                        // className={classes.allocationButton}
                        startIcon={<ClassTwoToneIcon />}
                      >
                        Details
                      </Button>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </CardContent>
          </Card>
        </div>
      ))}
    </section>
  );

  if (loading === true) {
    orderList = (
      <div style={{ marginTop: "100px" }}>
        <Spinner />
      </div>
    );
  }

  if (activeOrders == "") {
    if (loading === true) {
      return (
        <div>
          <Typography className={classes.title} gutterBottom>
            My Active Orders
          </Typography>
          <Spinner />
        </div>
      );
    } else {
      return (
        <div>
          <Typography className={classes.title} gutterBottom>
            My Active Orders
          </Typography>

          <Typography
            style={{ fontSize: 20, height: 50, padding: 10, paddingLeft: 55 }}
          >
            No Orders to Show, Try Placing a New order
          </Typography>
        </div>
      );
    }
  } else {
    return (
      <div>
        <Typography className={classes.title} gutterBottom>
          My Active Orders
        </Typography>

        {orderList}
      </div>
    ); //  block of code to be executed if the condition is false
  }
};

export default MyOrders;
